.section-1{
     width: 100%;
    display: block;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.section-1 .img-background {
    position: absolute;
    top: 0; left: 0;
    z-index: -5;
    min-height: 120vh; width: 100%;
    background-image: url("../../public/img/justice-woman.png");
    background-position: center;
    transform: scaleX(-1);
    background-repeat: no-repeat;
    
    filter: opacity(10%);
    
}
.section-1 .banner-1 .logo {
    width: 100%; max-height: 25%;
}
.section-1 .banner-1 .logo img{
    max-width: 100%;
    height: 12rem;
}

.section-1 .banner-1 {
    padding: 6rem 0.5rem 0;

    width: 100%; margin-bottom: 8rem;
    display: flex; flex-direction: column;
    align-items: center;
   
    
}
.section-1 .banner-1 .icons {
    width: 100%; height: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 8rem;
}
.section-1 .banner-1 .icons .icon-container img{
    max-width: 4rem;
}

.section-1 .banner-1 .title-1 {
    height: 40%;
    margin: 3rem 1.5rem;
    
    margin-bottom: 1rem;
}
.section-1 .banner-1 .title-1 h2 {
    font-family: 'Big Shoulders Display', cursive;
    color: #F25D40;
    font-weight: 400;
    line-height: 1;
    font-size: 58px;
    padding: 0 0.5rem;
    text-align: start;
}
.section-1 .banner-1 .title-1 p {
    display: none;
}
.section-1 .banner-1 .hr {
    width: 80%; 
    margin-left: auto; margin-right: auto;
    margin: 0 1rem;
    height: 3px;
    background-color: #F25D40;
    margin-bottom: 1rem;
}

.section-1 .banner-1 .arrow-icon-link {
    text-decoration: none;
    color: antiquewhite;
}
.section-1 .banner-1 .arrow-icon-link .arrow-icon {
    
    font-size: 2.5rem;
}
.section-1 .banner-1 .arrow-icon:hover {
    font-size: 3.2rem;
}


.section-1 .banner-2 {
    
    
    padding: 8rem 1.5rem 0;
    text-align: center;

    margin-bottom: 4rem;

}
.section-1 .banner-2 h2 {
    
    font-size: 42px;
    font-weight: 300;
    color: #F25D40;
    font-family: 'Big Shoulders Display', cursive;
    line-height: 0.9;
    letter-spacing: 1px;
    
    margin-bottom: 3rem;
    
}

.section-1 .banner-2 .info-container {
    height: 100%;
    margin-bottom: 8rem;
}
.section-1 .banner-2 .info-container .info {
    margin-bottom: 4rem;
}
.section-1 .banner-2 .info-container .info .info-title {
    display: flex; align-items: center; justify-content: center;
}
.section-1 .banner-2 .info-container .info .info-title h2 {
    font-size: 36px;
    font-weight: 400;
    color: antiquewhite;
    font-family: 'Big Shoulders Display', cursive;
    line-height: 0.9;
    letter-spacing: 1px;
    text-align: start;
    margin: 0;
    padding: 0 1rem;
}

.section-1 .banner-2 .info-container .info .info-title .info-icon {
    font-size: 3.5rem;
    color: #F25D40;
    margin-right: 1rem;

}
.section-1 .banner-2 .info-container .info .text p {
        color: antiquewhite;
        margin-top: 2rem;
        text-align: start;
        font-size: 20px;
        font-weight: 300;
        line-height: 1.2;
        letter-spacing: 1.2px;
}

.section-1 .banner-3 {
    width: 100%; 
    height: 100%;
    
}
.section-1 .banner-3 .info-container {
    display: flex; align-items: center; justify-content: center;
    height: 75vh;

    background-image: url('../../public/img/img-background-2.png');
    background-position: center;
    background-size: cover;
    background-attachment: fixed;
    

}


.section-1 .banner-3 .info-container h2 {
    
    position: absolute;
    
    color: antiquewhite;
    
    font-weight: 300;
    line-height: 1;
    letter-spacing: 1px;
    font-size: 24px;
    padding: 0 1rem;
    text-align: start;

    text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.692);
    text-align: center;
}

.section-1 .banner-4 {
    width: 100%;
    

}
.section-1 .banner-4 .form-contact {
    width: 100%;
    padding:6rem 0;
    background-color: rgba(226, 219, 211, 0.808);
    border-radius: 0.2rem;
    
}
.section-1 .banner-4 .form-contact h3 {
    color: black;
    padding: 0 25%;
}


.section-1 .banner-5  h2 {
    font-family: 'Big Shoulders Display', cursive;
    color: #F25D40;
    font-size: 42px;
    font-weight: 400;
    margin-bottom: 1rem;
    
}


.section-1 .banner-5 {
    padding: 8rem 1.5rem 0;
}
.section-1 .banner-5 h2 {
    line-height: 0.9;
    letter-spacing: 1px;
    margin-bottom: 3rem;
}
.section-1 .banner-5 .carousel {
    margin-bottom: 6rem;
}
.section-1 .banner-5 .carousel .carousel-inner{
    height: 100%;
}
.section-1 .banner-5 .carousel .carousel-inner .carousel-item{
    height: 100%; width: 100%;
   
}
.section-1 .banner-5 .carousel .carousel-inner .carousel-item .users {
    width: 100%; height: 100%;
    display: flex; flex-direction: column;
    align-items: center; justify-content: center;
}
.section-1 .banner-5 .carousel .carousel-inner .carousel-item .users .user-item {
    display: flex; flex-direction: column;
    align-items: center; justify-content: center;
   
   padding: 1rem 1rem;
}

.section-1 .banner-5 .carousel .carousel-inner .carousel-item .users .user-item .user-icon {
    font-size: 3rem; margin-bottom: 1rem;
}
.section-1 .banner-5 .carousel .carousel-inner .carousel-item .users .user-item h2 {
    font-size: 28px;
    letter-spacing: 1px;
    font-weight: 300;
    margin-bottom: 1rem;
    color: antiquewhite;
}
.section-1 .banner-5 .carousel .carousel-inner .carousel-item .users .user-item p {
    padding: 0 2rem;
}


@media only screen and (max-width: 420px){
    .section-1 .banner-1 .title-1{
        margin-bottom: 0.5rem;
    }
    .section-1 .banner-1 .title-1 h2 {
        font-size: 48px;
    }
    .section-1 .banner-1 .icons {
        margin-bottom: 3.5rem;
    }



    .section-1 .banner-3 .info-container h2 {
        font-size: 25px; font-weight: 200;
        letter-spacing: 1.2px;
    }
    .section-1 .banner-4 {
        width: 100%;
        padding:0;
    
    }
    .section-1 .banner-4 .form-contact {
        width: 100%;
        padding:6rem 0;
        background-color: rgba(226, 219, 211, 0.808);
        border-radius: 0.2rem;
        
    }
    .section-1 .banner-4 .form-contact h3 {
        color: black;
    }
}

