form {
    width: 100%;
    display: flex; flex-direction: column;
    align-items: center; justify-content: center;
}
form .form-level {
    width: 100%;
    padding: 0 25%;
    display: flex;
    flex-direction: column;
    margin-bottom: 1.5rem ;
}
form .form-level label {
    color: rgb(92, 92, 92);
}
form .form-level input {
    width: 100%; height: 3rem;
    text-align: center;
}
form .form-level textarea {
    text-align: center;
}
.not-form p {
    color: rgb(67, 126, 67);
}
.not-form-err p {
    color: rgb(67, 126, 67);
}
@media only screen and (max-width: 420px){
    form .form-level {
        width: 100%;
        padding: 0 5%;
        
    }
}